
.PictureBackgroundContainer {
    top: 0;
    bottom: 0;
    width: 100%;
    height: auto;
    background-color: #091d09;
    display: flex;
}

.PictureBackgroundImage {
    float: right;
    width: 60%;
    aspect-ratio: 3/4;
    z-index: 1;
    border-radius: 0;
    border-width: 0;
}

.PictureBackgroundText {
    float: left;
    width: 40%;
    color: #ffffff;
    padding-top: 60px;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    box-shadow: 40px 0px 40px 40px #091d09;
    z-index: 2;
}

.PictureBackgroundText h1 {
    font-size: 32pt;
    animation: fadein 1.5s;
}

.PictureBackgroundText p1 {
    font-size: 18pt;
    animation: fadein 4.0s;
    padding-top: 10px;
}

.PictureBackgroundText p2 {
    font-size: 18pt;
    animation: fadein 6.5s;
    padding-top: 10px;
}

@keyframes fadein {
    0% {opacity: 0}
    30% {opacity: 0}
    100% {opacity: 1}
}